/* TABLES*/
table {
  font-size: .9em;
}

table ul {
  font-size: 1em !important;
  /*margin-left: 5px !important;*/
}

.table-conteiner {
  overflow-x: auto;
}

.table-model-1 {
  max-width: 900px;
  table-layout: fixed;
  caption-side: bottom
}

.table-model-1, .table-model-1 th, .table-model-1 td {
  border: 1px solid #000;
  border-collapse: collapse;
  -webkit-transition: all .1s ease;
  -o-transition: all .1s ease;
  transition: all .1s ease
}

.table-model-1 caption {
  color: #888;
  font-size: .7em;
  padding: 5px
}

.table-model-1 th {
  padding: 5px 10px;
  background-color: #000;
  text-align: center;
  font: bold .8em var(--font-heading);
  color: #fff
}

.table-model-1 td {
  padding: 5px 10px 5px 8px;
  font-family: var(--font-rest);
  font-size: .8em
}
/* END TABLE */